"use client";

import { useState, useEffect } from "react";
import { User, Lock, Trash2, Eye, EyeOff, Check } from "lucide-react";
import { useAuth } from "@/lib/auth-context";
import { useRouter } from "next/navigation";

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_BASE_URL ?? "https://localhost:7105";

export default function PerfilPage() {
  const { user, token, logout } = useAuth();
  const router = useRouter();

  const [activeTab, setActiveTab] = useState<"dados" | "senha" | "excluir">(
    "dados"
  );

  const [nome, setNome] = useState("");
  const [sobrenome, setSobrenome] = useState("");
  const [email, setEmail] = useState("");
  const [celular, setCelular] = useState("");

  const [senhaAtual, setSenhaAtual] = useState("");
  const [novaSenha, setNovaSenha] = useState("");
  const [confirmarSenha, setConfirmarSenha] = useState("");
  const [showSenhaAtual, setShowSenhaAtual] = useState(false);
  const [showNovaSenha, setShowNovaSenha] = useState(false);

  const [confirmarExclusao, setConfirmarExclusao] = useState("");
  const [senhaExcluir, setSenhaExcluir] = useState("");

  const [savedDados, setSavedDados] = useState(false);
  const [savedSenha, setSavedSenha] = useState(false);

  const [erroSenha, setErroSenha] = useState("");
  const [erroExcluir, setErroExcluir] = useState("");
  const [msgExcluir, setMsgExcluir] = useState("");

  const [loadingSenha, setLoadingSenha] = useState(false);
  const [loadingExcluir, setLoadingExcluir] = useState(false);

  const [hydrated, setHydrated] = useState(false);

  // formata para exibir no input: (22) 99754-0815
  const formatPhone = (value: string): string => {
    const digits = value.replace(/\D/g, "").slice(0, 11); // m√°ximo 11 d√≠gitos

    if (!digits) return "";

    if (digits.length <= 2) {
      return `(${digits}`;
    }

    if (digits.length <= 6) {
      return `(${digits.slice(0, 2)}) ${digits.slice(2)}`;
    }

    if (digits.length <= 10) {
      return `(${digits.slice(0, 2)}) ${digits.slice(2, 6)}-${digits.slice(
        6
      )}`;
    }

    return `(${digits.slice(0, 2)}) ${digits.slice(2, 7)}-${digits.slice(
      7,
      11
    )}`;
  };

  const unmaskPhone = (value: string): string => value.replace(/\D/g, "");

  // Sinaliza quando o componente j√° est√° montado no browser
  useEffect(() => {
    setHydrated(true);
  }, []);

  // Preenche os campos com os dados do token
  useEffect(() => {
    if (user) {
      const firstName =
        (user.firstName as string | undefined) ??
        ((user.fullName as string | undefined)?.split(" ")[0] ?? "");
      const lastName = (user.lastName as string | undefined) ?? "";
      const userEmail = (user.email as string | undefined) ?? "";
      const userPhone = (user.phone as string | undefined) ?? "";

      setNome(firstName);
      setSobrenome(lastName);
      setEmail(userEmail);
      setCelular(formatPhone(userPhone));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [user]);

  // Se depois de hidratado n√£o houver usu√°rio, redireciona para login
  useEffect(() => {
    if (!hydrated) return;
    if (!user) {
      router.push("/auth/login");
    }
  }, [user, hydrated, router]);

  if (!hydrated) {
    return (
      <div className="bg-background border border-border rounded-xl p-12 text-center">
        <p className="text-foreground/60">Carregando...</p>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="bg-background border border-border rounded-xl p-12 text-center">
        <p className="text-foreground/60">Redirecionando...</p>
      </div>
    );
  }

  // üöÄ Detecta se a conta veio do Google pelo token
  const isGoogleAccount =
    typeof user.authProvider === "string" &&
    user.authProvider.toLowerCase() === "google";

  const handleSaveDados = () => {
    // depois voc√™ pode chamar a API de perfil para salvar (nome/sobrenome/email/phone)
    // const phoneToSend = unmaskPhone(celular);
    // no backend, se quiser, ignore o campo email quando isGoogleAccount === true

    setSavedDados(true);
    setTimeout(() => setSavedDados(false), 2000);
  };

  const handleChangeSenha = async () => {
    setErroSenha("");
    setSavedSenha(false);

    // üîí Bloqueia altera√ß√£o de senha para contas Google
    if (isGoogleAccount) {
      setErroSenha(
        "Sua conta est√° conectada ao Google. A senha √© gerenciada pelo Google e n√£o pode ser alterada aqui."
      );
      return;
    }

    if (!senhaAtual || !novaSenha || !confirmarSenha) {
      setErroSenha("Preencha todos os campos.");
      return;
    }

    if (novaSenha !== confirmarSenha) {
      setErroSenha("As senhas n√£o coincidem.");
      return;
    }

    if (!token) {
      setErroSenha("Sess√£o expirada. Fa√ßa login novamente.");
      router.push("/auth/login");
      return;
    }

    try {
      setLoadingSenha(true);

      const response = await fetch(
        `${API_BASE_URL}/api/user/change-password`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({
            currentPassword: senhaAtual,
            newPassword: novaSenha,
            confirmNewPassword: confirmarSenha,
          }),
        }
      );

      const data = await response.json().catch(() => ({}));

      if (!response.ok) {
        setErroSenha(data?.message || "N√£o foi poss√≠vel alterar a senha.");
        return;
      }

      setSavedSenha(true);
      setSenhaAtual("");
      setNovaSenha("");
      setConfirmarSenha("");
      setTimeout(() => setSavedSenha(false), 2500);
    } catch (error) {
      console.error("Erro ao alterar senha:", error);
      setErroSenha("Erro ao conectar ao servidor. Tente novamente.");
    } finally {
      setLoadingSenha(false);
    }
  };

  const handleExcluirConta = async () => {
    setErroExcluir("");
    setMsgExcluir("");

    if (confirmarExclusao !== "EXCLUIR") {
      setErroExcluir('Digite "EXCLUIR" para confirmar.');
      return;
    }

    if (!senhaExcluir) {
      setErroExcluir("Digite sua senha para confirmar a exclus√£o.");
      return;
    }

    if (!token) {
      setErroExcluir("Sess√£o expirada. Fa√ßa login novamente.");
      router.push("/auth/login");
      return;
    }

    const confirmar = window.confirm(
      "Tem certeza que deseja excluir sua conta? Essa a√ß√£o √© irrevers√≠vel."
    );
    if (!confirmar) return;

    try {
      setLoadingExcluir(true);

      const response = await fetch(
        `${API_BASE_URL}/api/user/account`,
        {
          method: "DELETE",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({
            currentPassword: senhaExcluir,
          }),
        }
      );

      const data = await response.json().catch(() => ({}));

      if (!response.ok) {
        setErroExcluir(
          data?.message ||
            "N√£o foi poss√≠vel excluir a conta. Verifique sua senha."
        );
        return;
      }

      setMsgExcluir("Conta exclu√≠da com sucesso.");
      // limpa auth e redireciona
      logout();
      router.push("/");
    } catch (error) {
      console.error("Erro ao excluir conta:", error);
      setErroExcluir("Erro ao conectar ao servidor. Tente novamente.");
    } finally {
      setLoadingExcluir(false);
    }
  };

  return (
    <div className="bg-background border border-border rounded-xl">
      {/* Tabs */}
      <div className="flex border-b border-border">
        <button
          onClick={() => setActiveTab("dados")}
          className={`flex items-center gap-2 px-6 py-4 text-sm font-medium transition-colors ${
            activeTab === "dados"
              ? "text-foreground border-b-2 border-primary"
              : "text-foreground/60 hover:text-foreground"
          }`}
        >
          <User size={18} />
          Meus Dados
        </button>
        <button
          onClick={() => setActiveTab("senha")}
          className={`flex items-center gap-2 px-6 py-4 text-sm font-medium transition-colors ${
            activeTab === "senha"
              ? "text-foreground border-b-2 border-primary"
              : "text-foreground/60 hover:text-foreground"
          }`}
        >
          <Lock size={18} />
          Alterar Senha
        </button>
        <button
          onClick={() => setActiveTab("excluir")}
          className={`flex items-center gap-2 px-6 py-4 text-sm font-medium transition-colors ${
            activeTab === "excluir"
              ? "text-red-500 border-b-2 border-red-500"
              : "text-foreground/60 hover:text-red-500"
          }`}
        >
          <Trash2 size={18} />
          Excluir Conta
        </button>
      </div>

      <div className="p-6">
        {/* Meus Dados Tab */}
        {activeTab === "dados" && (
          <div className="space-y-6">
            <div>
              <h2 className="text-xl font-medium text-foreground mb-2">
                Informa√ß√µes Pessoais
              </h2>
              <p className="text-sm text-foreground/60">
                Atualize suas informa√ß√µes de perfil
              </p>

              {/* Tipo de conta */}
              <p className="mt-2 text-xs text-foreground/60">
                Tipo de conta:{" "}
                <span className="font-medium">
                  {isGoogleAccount
                    ? "Conectada ao Google"
                    : "E-mail e senha"}
                </span>
              </p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Nome
                </label>
                <input
                  type="text"
                  value={nome}
                  onChange={(e) => setNome(e.target.value)}
                  className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Sobrenome
                </label>
                <input
                  type="text"
                  value={sobrenome}
                  onChange={(e) => setSobrenome(e.target.value)}
                  className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">
                E-mail
              </label>
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                disabled={isGoogleAccount}
                className={`w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none ${
                  isGoogleAccount
                    ? "text-foreground/70 bg-muted cursor-not-allowed"
                    : "focus:border-accent"
                }`}
              />
              {isGoogleAccount && (
                <p className="mt-1 text-xs text-foreground/60">
                  Este e-mail √© gerenciado pela sua conta Google e n√£o pode ser
                  alterado aqui.
                </p>
              )}
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">
                Celular
              </label>
              <input
                type="tel"
                value={celular}
                onChange={(e) => setCelular(formatPhone(e.target.value))}
                className="w-full px-4 py-3 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                placeholder="(22) 99754-0815"
              />
            </div>

            <button
              onClick={handleSaveDados}
              className="flex items-center gap-2 bg-primary text-primary-foreground px-6 py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors"
            >
              {savedDados ? <Check size={18} /> : null}
              {savedDados ? "Salvo!" : "Salvar Altera√ß√µes"}
            </button>
          </div>
        )}

        {/* Alterar Senha Tab */}
        {activeTab === "senha" && (
          <div className="space-y-6 max-w-md">
            <div>
              <h2 className="text-xl font-medium text-foreground mb-2">
                Alterar Senha
              </h2>
              <p className="text-sm text-foreground/60">
                {isGoogleAccount
                  ? "Sua conta est√° conectada ao Google. A senha √© gerenciada pelo Google e n√£o pode ser alterada aqui."
                  : "Atualize sua senha de acesso"}
              </p>
            </div>

            {erroSenha && (
              <p className="text-sm text-red-500 bg-red-50 border border-red-200 rounded-md px-3 py-2">
                {erroSenha}
              </p>
            )}

            {isGoogleAccount ? (
              // üîí Conta Google ‚Üí n√£o mostra campos de senha
              <div className="mt-2 rounded-md border border-border bg-muted px-3 py-3 text-sm text-foreground/70">
                Para alterar o m√©todo de acesso, remova ou ajuste o login com
                Google diretamente nas configura√ß√µes da sua conta Google.
              </div>
            ) : (
              <>
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Senha Atual
                  </label>
                  <div className="relative">
                    <input
                      type={showSenhaAtual ? "text" : "password"}
                      value={senhaAtual}
                      onChange={(e) => setSenhaAtual(e.target.value)}
                      className="w-full px-4 py-3 pr-12 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                    />
                    <button
                      type="button"
                      onClick={() => setShowSenhaAtual(!showSenhaAtual)}
                      className="absolute right-4 top-1/2 -translate-y-1/2 text-foreground/50 hover:text-foreground"
                    >
                      {showSenhaAtual ? (
                        <EyeOff size={18} />
                      ) : (
                        <Eye size={18} />
                      )}
                    </button>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Nova Senha
                  </label>
                  <div className="relative">
                    <input
                      type={showNovaSenha ? "text" : "password"}
                      value={novaSenha}
                      onChange={(e) => setNovaSenha(e.target.value)}
                      className="w-full px-4 py-3 pr-12 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                    />
                    <button
                      type="button"
                      onClick={() => setShowNovaSenha(!showNovaSenha)}
                      className="absolute right-4 top-1/2 -translate-y-1/2 text-foreground/50 hover:text-foreground"
                    >
                      {showNovaSenha ? (
                        <EyeOff size={18} />
                      ) : (
                        <Eye size={18} />
                      )}
                    </button>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Confirmar Nova Senha
                  </label>
                  <div className="relative">
                    <input
                      type={showNovaSenha ? "text" : "password"}
                      value={confirmarSenha}
                      onChange={(e) => setConfirmarSenha(e.target.value)}
                      className="w-full px-4 py-3 pr-12 border border-border rounded-lg bg-background focus:outline-none focus:border-accent"
                    />
                    <button
                      type="button"
                      onClick={() => setShowNovaSenha(!showNovaSenha)}
                      className="absolute right-4 top-1/2 -translate-y-1/2 text-foreground/50 hover:text-foreground"
                    >
                      {showNovaSenha ? <EyeOff size={18} /> : <Eye size={18} />}
                    </button>
                  </div>
                </div>
                
                {/* ALERTA DE SUCESSO COM ANIMA√á√ÉO */}
                {savedSenha && (
                  <div className="flex items-center gap-2 mt-3 px-4 py-3 rounded-lg bg-emerald-50 border border-emerald-200 text-emerald-700 text-sm animate-fade-in">
                    <Check size={18} className="text-emerald-700" />
                    Sua senha foi alterada com sucesso!
                  </div>
                )}

                <button
                  onClick={handleChangeSenha}
                  disabled={loadingSenha}
                  className="flex items-center gap-2 bg-primary text-primary-foreground px-6 py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors disabled:opacity-50"
                >
                  {loadingSenha ? "Salvando..." : "Alterar Senha"}
                </button>
              </>
            )}
          </div>
        )}

        {/* Excluir Conta Tab */}
        {activeTab === "excluir" && (
          <div className="space-y-6 max-w-md">
            <div>
              <h2 className="text-xl font-medium text-red-500 mb-2">
                Excluir Conta
              </h2>
              <p className="text-sm text-foreground/60">
                Esta a√ß√£o √© irrevers√≠vel. Todos os seus dados ser√£o
                permanentemente exclu√≠dos.
              </p>
            </div>

            <div className="bg-red-50 border border-red-200 rounded-lg p-4">
              <p className="text-sm text-red-700">
                Ao excluir sua conta:
              </p>
              <ul className="mt-2 text-sm text-red-600 list-disc list-inside space-y-1">
                <li>Todos os seus pedidos ser√£o desvinculados</li>
                <li>Seus endere√ßos salvos ser√£o removidos</li>
                <li>Voc√™ perder√° acesso a todo hist√≥rico</li>
              </ul>
            </div>

            {erroExcluir && (
              <p className="text-sm text-red-500 bg-red-50 border border-red-200 rounded-md px-3 py-2">
                {erroExcluir}
              </p>
            )}

            {msgExcluir && (
              <p className="text-sm text-emerald-600 bg-emerald-50 border border-emerald-200 rounded-md px-3 py-2">
                {msgExcluir}
              </p>
            )}

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">
                Sua senha
              </label>
              <input
                type="password"
                value={senhaExcluir}
                onChange={(e) => setSenhaExcluir(e.target.value)}
                className="w-full px-4 py-3 border border-red-300 rounded-lg bg-background focus:outline-none focus:border-red-500"
                placeholder="Digite sua senha atual"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">
                Digite <span className="font-bold">EXCLUIR</span> para
                confirmar
              </label>
              <input
                type="text"
                value={confirmarExclusao}
                onChange={(e) => setConfirmarExclusao(e.target.value)}
                className="w-full px-4 py-3 border border-red-300 rounded-lg bg-background focus:outline-none focus;border-red-500"
                placeholder="EXCLUIR"
              />
            </div>

            <button
              onClick={handleExcluirConta}
              disabled={confirmarExclusao !== "EXCLUIR" || loadingExcluir}
              className="bg-red-500 text-white px-6 py-3 rounded-lg font-medium hover:bg-red-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {loadingExcluir ? "Excluindo..." : "Excluir Minha Conta"}
            </button>
          </div>
        )}
      </div>
    </div>
  );
}